*{
    margin: 0px;
    padding: 0px;
}
.no1_5_nav{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}
.no1_5_nav_1{
    width: 70%;
    border-radius: 1%;
    background: #fff;
    box-sizing: border-box;
    height: 100%;
}
.no1_5_title{
    height: 50px;
    overflow: hidden;
    line-height: 50px;
    padding:0px 20px;
    border-bottom: 3px solid #ddd;
    box-sizing: border-box;
    margin-bottom: 20px;
}
.no1_5_title div{
    float: left;
    box-sizing: border-box;
}
.no1_5_title div:nth-of-type(2){
    float: right;
}
.mask{
    background: rgba(0,0,0,0.4) !important;
    z-index: 10;
    height: 100vh;
    position: fixed;
    width: 100vw;
  }
  .selectMask_box{
    background: rgba(0,0,0,0);
    transition: all .2s linear
  }
  .ant-table-header{
    margin-bottom:-5px !important;
    padding-bottom:0px !important;
}
.ant-tabs{
    line-height: 26px;
}
.no1_5_Modal ul li{
    width: 49%;
    margin: 0px 1% 30px 0px;
    float: left;
}
.no1_5_Modal ul li h3{
    margin-bottom: 20px;
}
.no1_5_Modal ul li input{
    width: 80%;
    height: 40px;
    border: 1px solid #ddd;
    text-indent: 1em;
}
.no1_5_title_2 button{
    border: none;
    margin-top: 0px;
}
.no1_5_Modal_1{
    height: 205px;
}
.no1_5_Modal_2{
    height: 110px;
}
.no1_5_Modal_2 div{
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-right: 10px;
    border: 1px solid #ddd;
    border-radius: 50%;
    float: left;text-align: center;
}
.no1_5_Modal_3{
    height: 200px;
    border: none;
    margin-bottom: 30px;
}
.no1_5_Modal_3 textarea{
    text-indent: 1em;
    width: 100%;
    height: 150px;
    border: 1px solid #ddd;
    outline: none;
}
.no1_5_Modal_4{
    height: 32px;
}
.no1_5_Modal_4 button{
    border: none;
}
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector{
    height: 32px;
}
.no1_5_day{
    width: 100%;
}
textarea {
    width: 100%;
    resize: none; 
    outline: none;
    text-indent: 1em; 
 }

 
 .no1_5_content{
     height: calc(100% - 70px);
 }
 .no1_5_1_title{
     height: 100%;
 }
 