*{
    margin: 0px;
    padding: 0px;
}
.no1_3_2_title input{
    border: 1px solid #ddd;
    width: 328px;
    height: 36px;
    text-indent: 1em;
    background: #fff;
    
}
.no1_3_2_input{
    margin-left: 5%;
}
.no1_3_2_title div{
    min-height: 100px;
}
.no1_3_2_title ul{
    padding: 0px 5%;
}
.no1_3_2_title ul li{
    height: 37px;
    line-height: 37px;
    float: left;
    margin-bottom: 20px;
    margin-right: 10px;
}
.no1_3_2_title ul li:nth-of-type(2n-1){
    width: 65px;
}
.no1_3_2_title ul li:nth-of-type(2n){
    width: 115px;
}
.DownOutlined{
    float: right;
}
.no1_3_2_content{
    width: 100%;
    min-height: 100px;
    margin-top: 20px;
    text-align: center;
}
.no1_3_2_title ul li:nth-of-type(2) .ant-cascader-menu{
    height: 134px;
}
